.player {
    width: 100vw;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    background-color: $darkgrey;

    &__wrap {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        grid-template-rows: 100%;
        grid-gap: 30px;
        justify-content: center;
        align-items: center; }

    &__title {
        display: block;
        width: 100%;
        text-align: center;
        color: $white;
        margin-bottom: 10px; }

    &__controls {
        width: 300px;
        max-width: 50%;
        margin: 0 auto;
        padding: 20px 0 40px 0;
        position: relative; }

    &__playback {
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        position: relative;
        cursor: pointer; }

    &__icon {
        display: block;
        max-width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transition: opacity .3s ease, transform .3s ease;

        &_play {
            opacity: 1; }

        &_pause {
            opacity: 0; } }

    &__row {
        display: flex;
        align-items: center;

        &_center {
            justify-content: center; }

        &_between {
            justify-content: space-between; } }

    &__cover {
        width: 100%;
        height: 100%;
        padding: 10px 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        transform: perspective(500px) rotateY(-10deg);

        img {
            display: block;
            max-width: 80%;
            max-height: 100%;
            background-color: lighten($maingrey, 20%); } }

    &__volume {
        position: absolute;
        z-index: 10;
        width: 30%;
        bottom: 35px;
        right: -35%;

        &:hover {

            .slider {
                opacity: 1;
                pointer-events: all; } }

        .player__icon {
            width: 15px;
            position: relative;

            // &:hover

            //     & ~ .slider
            //         opacity: 1
 }            //         pointer-events: all

        .slider {
            width: 70px;
            opacity: 0;
            pointer-events: none;
            position: absolute;
            top: 50%;
            transform: translate(25px, -50%);
            margin-top: 0;
            transition: opacity .3s ease; } }


    &_playing {

        .player {

            &__icon {

                &_play {
                    opacity: 0; }

                &_pause {
                    opacity: 1; } } } } }

.logo {
    min-height: 100%;
    width: 150px;
    display: flex;
    align-items: center;

    &__link {
        display: block; }

    &__img {
        display: block;
        width: 100%; } }
