.App {
    background-color: $maingrey; }

.container {
    width: 85%;
    margin: 0 auto; }

.radio {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    min-height: 100vh;
    padding: 200px 0 150px 0;

    &__title {
        width: 100%;
        color: $white;
        padding: 20px 0; }

    &__wrap {
        max-width: 2000px;
        width: 100%;
        display: grid;
        justify-content: space-between;
        grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
        grid-gap: 2vw;
        padding: 0 20px; } }

.load {
    color: #fff;
    white-space: nowrap;
    border: 2px solid $orange;
    background-color: $orange;
    line-height: 40px;
    font-size: 20px;
    padding: 0 20px;
    margin-top: 50px;
    transition: color .3s ease, background-color .3s ease;

    &:hover {
        color: $orange;
        background-color: transparent; } }
